.mainContainer{
  text-align: center;
  color:white;
}

h1{
  background-color: #001f3f;
  font-size: 3rem;
  letter-spacing: 20px;
  margin: 10px 50px 20px;
  padding: 20px;
}

.inputTextbox,
.addButton{
  font-size: 2rem;
  border: 2px solid #0074D9;
}

.addButton{
  margin: 10px;
  background-color: white;
  color: black;
}

.addButton:hover{
  cursor:pointer;
}

.listItem{
  list-style-type: none;
  background-color: #0074D9;
  margin: 10px auto;
  width: 420px;
  padding-left:1em;
  font-size: 2rem;
  text-align: left;
  transition: all .2s ease-in-out;
}

.listItem:hover{
  cursor:pointer;
  transform: scale(1.1);
}

